.navbar__container{
    width: 100vw;
    height: 8vh;
    display: grid;
    position: fixed;
    top: 0;
    grid-template-columns: 50% 50%;
    justify-content: center;
    align-items: center;
    background: #1f1e1d;
    color: #fff;
    z-index: 999;
}
.navbar__text{
    margin-left: 2vw;
    font-family: 'Sacramento', cursive;
}

.navbar__links-wrapper{
    display: flex;
    list-style: none;
    z-index: 999;
}

.navbar__link-item{
    margin-left: 3rem;
    margin-right: 3rem;
    padding: 0.8rem;
    font-size: 1.3rem;
    border-bottom: 4px solid transparent;
    transition: all 0.3s ease;
}

.navbar__link-item:hover{
    border-bottom: 4px solid rgb(255, 49, 49);
}

.navbar__menu-icon{
    display: none;
}

@media screen and (max-width: 1024px){
    .navbar__container{
        width: 100%;
    }

    .navbar__text{
        font-size: 1rem;
    }

    .navbar__menu-icon{
        display: block;
        font-size: 1.5rem;
        position: fixed;
        right: 1rem;
        top: 1rem;
        z-index: 99;
    }

    .navbar__links-wrapper{
        position: absolute;
        left: -100%;
        display: flex;
        flex-direction: column;
        text-align: center;
        width: 100vw;
        height: 100vh;
        margin-top: 2vh;
        padding-top: 7vh;
        line-height: 5rem;
        background: #1f1e1d;
        transition: all 0.5s ease-out;
        opacity: 0;
    }

    .navbar__active{
        left: 0;
        opacity: 1;
    }

}

@media screen and (width: 1024px){
    .navbar__menu-icon{
        font-size: 2rem;
        top: 2.5rem;
    }
}
